<template>
  <div id="education_add_teacher">
    <div class="add_teacher_container">
      <EcRouterBack  routerName='teacher'  />
      <div class="main">
        
        <el-form ref="form" :model="teacherMessage" label-width="80px">
          <el-row>
            <el-col :span="7">
              <el-form-item label="姓名">
                <el-input v-model="teacherMessage.name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :push="9" :span="7">
              <el-form-item label="性别">
                <div style="width:60%">
                  <el-select v-model="teacherMessage.sex">
                    <el-option label="男" value="1"></el-option>
                    <el-option label="女" value="2"></el-option>
                  </el-select>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="7">
              <el-form-item label="手机号码">
                <el-input type="number" v-model="teacherMessage.phone"></el-input>
              </el-form-item>
            </el-col>
            <el-col :push="9" :span="7">
              <el-form-item label="资格证号">
                <el-input v-model="teacherMessage.certificateNo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="7" >
              <el-form-item label="教学范围">
                <el-select multiple v-model="teachAreasDictValue">
                  <el-option
                    v-for="item in teachAreasDict"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            
            <el-col :push="9" :span="7" >
              <el-form-item label="科目">
                <div >
                  <el-select multiple v-model="subjectDictValue">
                    <el-option
                      v-for="item in subjectDict"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </div>
              </el-form-item>
            </el-col>
            
          </el-row>

          <el-row>
            <el-col :span="15">
              <el-form-item label="荣誉">
                <div class="text_area">
                  <el-input
                    rows="6"
                    show-word-limit
                    maxlength="200"
                    type="textarea"
                    v-model="teacherMessage.honor"
                    placeholder="每行一个"
                  ></el-input>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div class="_upload">
          <div >
            <input
              class="fileBut"
              ref="honorImg"
              @change="save_uploadSingle('honorImg')"
              type="file"
            />
            <img v-show="honorImg!=''" :src="honorImg" alt />
          </div>
          <div>
            <input class="fileBut" ref="photo" @change="save_uploadSingle('photo')" type="file" />
            <img v-show="photo!=''" :src="photo" alt />
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <button @click="save_pull()">提交</button>
    </div>
    
  </div>
</template>

<script>
import { addTeacher, teacherList, getDict ,updateTeacher} from '@/api/teacher.js'
import { uploadSingle } from '@/api/user.js'
import Utils from '@/utils/deep.js';
import EcRouterBack from "@/components/EduactionRouterBack.vue"
export default {
  data() {
    return {
      teacherMessage: {
        honorImg: '',//3
        certificateNo: '', //1
        honor: '', //2
        name: '', //4
        phone: '', //5
        photo: '',//6
        sex: '', //7
        subjectDict: '',//8
        teachAreasDict: '' //9
      },
      honorImg: '../../../static/img/teacher/certificate_upload.png',
      photo: '../../../static/img/teacher/photo_upload.png',
      imgUrl: [],
      teachAreasDict: [],
      subjectDict: [],
      teachAreasDictValue:[],
      subjectDictValue:[]
    }
  },
  components:{
    EcRouterBack
  },
  created() {
    this.save_getDict('subject')
    this.save_getDict('teachAreas')
    if (this.$route.query.name) {
      this.save_teacherList(this.$route.query.name)
    }
  },
  methods: {
    save_pull(){
      if(this.$route.query.name){
        this.save_updateTeacher()
      }else{
        this.save_addTeacher()
      }
    },
    //getDict
    save_getDict(name) {
      getDict(`?groupType=${name}`).then(res => {
        if (res.code == 200) {
          if (name == 'subject') {
            this.subjectDict = res.data
          } else if (name == 'teachAreas') {
            this.teachAreasDict = res.data;
            
          } 
        }
      })
    },
    //
    save_teacherList(name) {
      teacherList({
        current: 1,
        name: name,
        phone: '',
        size: 1
      }).then(res => {
        if (res.code == 200) {
          this.teacherMessage = res.data.records[0];
          this.teachAreasDictValue=res.data.records[0].teachAreasDict.split(",");
          this.subjectDictValue=res.data.records[0].subjectDict.split(",");
        }
      })
    },
    //  新增教师
    save_addTeacher() {
      this.teacherMessage.subjectDict = this.subjectDictValue.join(',');
      this.teacherMessage.teachAreasDict = this.teachAreasDictValue.join(',');
      addTeacher(this.teacherMessage).then(res => {
        if (res.code == 200) {
          this.$message({
            message: '提交成功',
            type: 'success',
            onClose: () => {
              this.$router.go(-1)
            }
          })
        }
      })
    },
    // 编辑教师
    save_updateTeacher(){
      this.teacherMessage.subjectDict = this.subjectDictValue.join(',');
      this.teacherMessage.teachAreasDict = this.teachAreasDictValue.join(',');
      updateTeacher(this.teacherMessage).then( res =>{
         this.$message({
            message: '编辑成功',
            type: 'success',
            onClose: () => {
              this.$router.go(-1)
            }
          })
      })
    },
    save_uploadSingle(name) {
      let dataFoles, files
      let REG = /\\/g
      dataFoles = this.$refs[name].files
      if (dataFoles.length != 0) {
        files = dataFoles[0]
        if (files.type !== 'image/png' || files.type !== 'image/jpg'  || files.type !== 'image/jpeg') {
          this.$message.error('图片格式必须为 .png, .jpg 或者 .jpeg 格式')
        } else
        if (files.size > 10240) {
          this.$message.error('请选择10MB以内的图片！')
        } else {
          let formData = new FormData()
          formData.append('file', files)
          uploadSingle(formData, 'agency').then(res => {
            if (res.code == 200) {
              this[name] = Utils.getObjectURL(this.$refs[name].files[0])
              if (name == 'honorImg') {
                this.imgUrl.push(res.data)
                this.teacherMessage[name] = this.imgUrl.join(',')
              } else {
                this.teacherMessage[name] = res.data
              }
            } else {
              this.$message.error('上传失败')
            }
          })
        }
      }
    }
  }
}
</script>

<style lang="scss">
@import '@/styles/mixin.scss';
@import '@/styles/variables.scss';
#education_add_teacher {
  height: 100%;
  background-color: $view-bgcolor;
  overflow: hidden;
 
  .footer {
    padding-top: 30px;
    button {
      @include buttonColor('c') {
        width: 200px;
        display: block;
        margin: auto;
      }
    }
  }
}

.add_teacher_container {
  width: 80%;
  height: 80%;
  margin-top: 20px;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  margin-left: 120px;
  position: relative;
  background: $view-container-bgcolor;
  position: relative;
  .main {
    width: 75%;
    height: 85%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    .el-form-item__label {
      color: $font-color;
      font-weight: 500;
      text-align-last: justify;
      text-align: justify;
    }
    ._upload {
      width: 100%;
      @include display(center);
      position: absolute;
      bottom: 20px;
      div {
        width: 100px;
        height: 100px;
        cursor: pointer;
        transition: all 0.1s;
        background-size: cover;
        background-repeat: no-repeat;
        margin: 0 40px;
        position: relative;
        &:nth-child(1) {
          // background-image: url(../../../static/img/teacher/certificate_upload.png);
          // &:hover {
          //   background-image: url(../../../static/img/teacher/certificate_upload_active.png);
          // }
        }

        &:nth-child(2) {
          // background-image: url(../../../static/img/teacher/photo_upload.png);
          // &:hover {
          //   background-image: url(../../../static/img/teacher/photo_upload_active.png);
          // }
        }
        .fileBut {
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          opacity: 0;
          width: 100px;
          height: 100px;
          z-index: 1;
        }
        img {
          position: absolute;
          top: 0;
          left: 0;
          height: 100px;
        }
      }
    }
    .text_area {
      @include scroll-bar;
      height: 150px;
      overflow: auto;
    }
  }
}
</style>



